.footerContent
{
    background-color: #303440;
    width: 100vw;
    height: 30rem;
    margin-top: -2rem;
    padding-top: 45px;
    padding-bottom: 10px;
    color: #cccccc;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border: none;
   

}


.footer
{
    
    width: 100vw;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 300;
    bottom: 0;
    font-size: small;
    box-shadow: 0px -5px 10px rgba(0, 0, 0, 0.3);
    margin-top: 2rem;
    padding: 2rem;
    background-color:  #303440;
    
}


.Footer-container
{

    width: 85vw;
    height: auto;
    display: flex;
    margin-left: 8rem;
    margin-top: 4rem;

    

}

.AboutUs
{
    /* background-color: gray; */
    width: 60vw;
    margin: 0 2rem;
    padding: 1rem;

}

.under
{
    background-color: #289dcc;
    height: 2px;
    margin-top: -0.5rem;
}
.heading1
{
    background-color: #289dcc;
    width: fit-content;
    padding: 0.2rem 0.8rem;
    font-size: 1.5rem;
    color: white;

}

.AboutPara
{
    margin-top: 1rem;
    font-size: 1rem;
    margin-left: 0.5rem;
}

.email
{
    color: aliceblue;
    margin-left: 0.5rem;
}

.emailid
{
    font-weight: bolder;
    color: white;
    margin-left: 0.5rem;
}
.emailid:hover
{
    color: #289dcc;
}

.social
{
    margin-top: 1.5rem;

}
.WriteForUs
{
    
    width: 60vw;
    margin:0 2rem;
    padding: 1rem;
    
}

.FollowUs
{
    width: 60vw;
    padding: 1rem;
}

.social
{

    height: 10rem;
    margin-left: 0.5rem;
    margin-top: -0.01px;
    margin: 1rem ;
    
}

.whiteLine
{
    background-color: white;
    height: 2px;
    width: 100vw;
    max-width: 80rem;
    margin: 0 auto;
    border-radius: 2rem;
    margin-top: -2rem;
    

}
.SocialHandle
{
    cursor: pointer;
    text-decoration: none;
    color: rgb(231, 231, 231);
    

}

.SocialHandle:hover
{
    color: #289dcc;
    
}

@media screen and (max-width:750px)
{
    .footerContent
    {
        height: fit-content;
        margin: 0;
    }

    .Footer-container
    {
        font-size: small;
        display: flex;
        flex-direction: column;
        margin: 0 2rem;
    }

    .AboutUs
    {

        width: 20rem;
        margin: 0;
    }

    .WriteForUs
    {

        width: 20rem;
        margin: 0;

    }
    .FollowUs
    {
        width: 20rem;
        margin: 0;
  
    }
}


