.Kmph
{
    
    width: 60vw;
    margin: 2rem 8rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    /* background-color: grey; */
}


@media screen and (max-width: 768px) {
    .Kmph
    {
        max-width: 100vw;
        width: 100vw; 
        padding: 0;
        margin: 0;
        
        
    }
    .lyricsPara
    {
        
        max-width: 95vw;
        margin: 0 auto;     
        padding: 1rem; 

    }
    .lyrics
    {
        margin-top: 2rem;
        font-size: 1.3rem;
        
        
    }
    

    .contactPage
    {
     
      background-color: #303440;
      color: white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;

    }

    .contactBt
    {

        padding:0.5rem 1.5rem;
        border-radius: 18px;
        text-align: center;
        background-color: #289dcc;
        color: white;
        font-size: 1.1rem;
        font-weight: bold;

    
    }

    .songTitle
    {

        font-weight: 600;
        font-size: 2rem;
        background-color:#303440;
        color: white;
        max-width: 90vw;
        margin: 0 auto;
        text-align: center;
        border-radius: 0.5rem;
        padding: 0 1rem;
       
    }

    .capoTitle
    {
        max-width: 100vw;
        margin: 0 1rem; 
        font-size: 1.3rem;
    }  

    .bold
    {
        font-size: 1.3rem;
    }

    .KMPHImg
    {
        width: 100vw;
        max-width: 100vw;
    }
}