.Aadat
{
    width: 60vw;
    margin: 2rem 8rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    /* background-color: grey; */
   
}

.bold
{
    font-weight: bolder;
    background-color: #289dcc;
    border-radius: 0.5rem;
    
}

.capoTitle
{ 
    font-weight: bolder;
}

.lyrics
{
    margin-top: 2rem;
    font-size: 20px;
}

.lyricsPara
{
    margin-top: 2rem;
    margin-left: 1rem;

}

.songTitle
{
    font-weight: 600;
    font-size: 3rem;
    background-color: #289dcc;
    width: fit-content ;
    text-align: center;
    border-radius: 0.5rem;
    padding: 0 1rem;
}




.Contactheading
{
    font-size: 2rem;
    font-weight: 600;
    margin: 0 0.5rem;

}
.Name
{
    width: 30vw;
    border-radius: 1rem;
    padding: 0.5rem;
    margin: 0.5rem auto;
    
}

.msg
{
    width: 30vw;
    border-radius: 1rem;
    height: 8rem;

}

.contactBt
{
    padding:0.5rem 1.5rem;
        border-radius: 18px;
        text-align: center;
        background-color: #289dcc;
        color: white;
        font-size: 1.1rem;
        font-weight: bold;
    transition: transform 0.5s;
     
    
}

.contactBt:hover
{
    transform: scale(1.1);
}

@media screen and (max-width: 768px) {
    .Aadat
    {
        max-width: 100vw;
        width: 100vw; 
        padding: 0;
        margin: 0;
        
    
                
    }
    .lyricsPara
    {
        
        max-width: 95vw;
        margin: 0 auto;     
        padding: 1rem; 

    }
    .lyrics
    {
        margin-top: 2rem;
        font-size: 10px;
        
        
    }
    

    .contactPage
    {
     
      background-color: #303440;
      color: white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;

    }

    .contactBt
    {

        padding:0.5rem 1.5rem;
        border-radius: 18px;
        text-align: center;
        background-color: #289dcc;
        color: white;
        font-size: 1.1rem;
        font-weight: bold;

    
    }

    .songTitle
    {

        font-weight: 600;
        font-size: 2rem;
        background-color:#303440;
        color: white;
        max-width: 90vw;
        margin: 0 auto;
        text-align: center;
        border-radius: 0.5rem;
        padding: 0 1rem;
       
    }

    .capoTitle
    {
        max-width: 100vw;
        margin: 0 1rem; 
        font-size: 1rem;
    }  

    .bold
    {
        font-size: 1.5rem;
    }

    .AadatImg
    {
     width: 100vw;
     max-width: 100vw;
     height: 30vh;
    }
