.intro
{
    padding: 0 7rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin-top: 15rem;

}

.h1Intro
{
    margin-top: 15rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    text-align: center;
    color: black;
    font-size: 2.3rem;


}
.h2Intro
{

 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center; 
 text-align: center;
 color: black;
}
.intropara
{

    font-size: 1.2rem;
    margin-top: 2rem;


}

.chordsHeading
{
    
    width: 100vw;
    max-width: 39.4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    text-align: center;
    margin:0 auto;
    background-color: rgb(50, 50, 50);
    font-size: 2rem;
    color: white;
    padding:10px 5px;
    margin-bottom: 1rem;

}
.second
{
    color:#289dcc;
}

.ChordImgs
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    flex-wrap: wrap;
    max-width: 45rem;
    margin: 0 auto;
    
}

.ChordImg
{
    padding: 1rem;
    height: 12rem;
    width: 10rem;
    border-radius: 0.8rem;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    transition: transform 0.5s;
    
}

.ChordImg:hover{

    transform: scale(1.1);
}

.Guitar
{
    width: 100vw;
    height: 110vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity:0.9;
    
    
}
.heading
{
    color: white;
    font-weight: bolder;
    
}

.hello
{
    font-size: 3rem;
    font-weight: 700;


}
@keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

.introname
{
    color: #289dcc;
    background-color: rgb(50, 50, 50);
    padding: 0 1rem;
    border-radius: 0.6rem;
    animation: fadeIn 1s ease-out;
}


.welcome
{
    width: 40vw;
    padding: 2rem 0;
    font-size: 3rem;
    font-weight: 700;
    flex-direction: column;
    justify-content: center;
    /* background-color: gray; */
    margin-top: 9rem;

}
.main-content
{
   
    margin-top: -10rem;
}

.intropara
{

    width: 46rem;
    display: flex;
}

@media only screen and (max-width: 768px) {
    
    .intro
    {
        margin: 0 auto;
        /* background-color: grey; */
        animation: none;
        padding: 0;
    }

    .main-content
    {

        height: fit-content;
        width: fit-content;
        padding: 1rem;
    }

    .intropara
    {
        display: flex;
        margin: 0;
        font-size: medium;
        padding: 1rem;

        width: 22rem;
    }

    .h1Intro
    {

        padding: 0 1rem;
        font-size: 2rem;
        font-weight: bolder;
        margin: 0;
        margin-top: 20rem;
        display: flex;
        
    }

    .h2Intro
    {
        margin-top: 1rem;
    }

    .chordsHeading
    {
        font-size: 2.3rem;
        padding:0;
        height: 8rem;
        width: 20rem;
    }
    .ChordImg
    {
        height: 10rem;
        width: 8rem;        
    }
    .Guitar
    {
        width: 100vw;
        max-width: 100vw;
        height: 100vh;
    }

}
