.HaleDil
{
    width: 60vw;
    margin: 2rem 8rem;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
}

@media screen and (max-width: 768px) {
    .HaleDil
    {
        max-width: 100vw;
        width: 100vw; 
        padding: 0;
        margin: 0;            
    }

    .lyricsPara
    {
        width: 100vw;
        max-width: 95vw;
        margin: 0 auto;     
        padding: 1rem;



    }
    .lyrics
    {
        margin-top: 2rem;
        font-size: 1.3rem;
        margin: 0;
        
        
    }
    

    .contactPage
    {
        width: 100vw;
        max-width: 100vw;
        height: 40vh;
      background-color: #303440;
      color: white;
      font-size: medium;
      

      

    }

    .contactBt
    {

        padding:0.5rem 1.5rem;
        border-radius: 18px;
        text-align: center;
        background-color: #289dcc;
        color: white;
        font-size: 1.1rem;
        font-weight: bold;
    }

    .songTitle
    {

        font-weight: bolder;
        font-size: 2rem;
        background-color:#303440;
        color: white;
        max-width: 90vw;
        margin: 0 auto;
        text-align: center;
        border-radius: 0.5rem;
        padding: 0 1rem;
       
    }

    .capoTitle
    {
        max-width: 100vw;
        margin:0 1rem; 
        font-size: 1.3rem;
    }  

    .bold
    {
        font-size: 1.3rem;
    }
    .HDImg
    {
        width: 100vw;
        max-width: 100vw;
    }
    .Name
    {
        width: 100vw;
        max-width: 80vw;
    }

    .msg
    {
        width: 100vw;
        max-width: 80vw;
    }
