    margin : 0 auto;
    margin-top: 3rem; 
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    
    
}
.s1
{
    font-size: 1.7rem;
    font-weight: 800;
    text-align: center;
    display: flex;
    justify-content: center;
}

.l1
{
    margin: 0.5rem;
}

