
.navbar
{
    width: 100vw;
    height: 4rem;
    margin: 0 auto;
    padding: 0 1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0 ;
    z-index: 3;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2);
    background-color: white;
    
}

.logo
{
    color: #289dcc;
    font-size: 1.5rem;
    object-fit: cover;
   


}

.desktopMenuListItem
{
    margin: 1rem;
    cursor: pointer;
    position: relative;
    text-decoration: none;
    color: #303440; /* Set your desired text color */
    padding: 10px 20px;
    display: inline-block;
    overflow: hidden;

}

.desktopMenuListItem::after
{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px; /* Set the initial height of the underline */
    bottom: 0;
    left: 0;
    background-color: #289dcc; /* Set your desired underline color */
    transform-origin: bottom left; /* Set the transform origin to bottom left */
    transform: scaleX(0); /* Initially set the scaleX to 0 for animation */
    transition: transform 0.3s ease;
    
}
.desktopMenuListItem:hover::after
{
    transform: scaleX(1);
}


.desktopMenuListItem
{
    padding: 0 0.2rem;
    font-size: 1.1rem;
    text-decoration: none;    
}

.DesktopMenu
{
    margin-right: -6rem;
}

.btn
{
    outline: #289dcc;
}

.logo
{
    width: 100vw;
    max-width: 10rem;
    height: 2rem;
    border-radius: 0.3rem;
    cursor: pointer;
    
}

.ArrowUP
{
    height: 1rem;
    width: 1rem;
    position: fixed;
    bottom: 0;
    right: 0;
    z-index:  9999;
    margin: 4rem;
    cursor: pointer;
}

.highlight {
    background-color: yellow;
    font-weight: bold;
  }


  
  .SearchBar
  {
      display: flex;
  }



  .NavMenu
{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

.listItem
{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);

}

.MobMenu
{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}


.alert
 {
    position: fixed;
    top: 56px; /* Height of the navbar */
    width: 100%;
    z-index: 1000;

    background-color:  #289dcc;
    max-width: 90vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    font-weight: bold;
    margin-top: 0.5rem;
    padding: 0.5rem;
    border-radius: 10px;
    display:none;
  
    
  }

  .AlertBtn
  {
    padding: 0 0.5rem;
    border-radius:10px;
    margin: 0 1rem;
  }
  @media screen and (max-width: 768px) {
    .navbar {
        margin: 0 auto;
        width: 100vw;
        max-width: 100vw;
    }

    .MobMenu
    {
        display: flex;
        height: 3rem;
        width: 3rem;
        border-radius:0.5rem ;
        padding: 0;
    }    
    .desktopMenuBtn
    {
        display: none;
    }

    .logo
    {
        margin: 0 auto;
    }

    .navbar
    {
        margin: 0 auto;
    }



    .DesktopMenu
    {
        display: none;
    }

    .SearchBar
    {
        display: none;
    }

    .alert
    {
        width: 100vw;
        max-width: 96vw;
        margin: 0.6rem 0.5rem;
        font-weight: bolder;
        display:flex;
        height: 6vh;
        justify-self: center;
        align-items: center;
        font-size: small;

      
    }
    .AlertBtn
    {
        padding:0.5rem 1.4rem;
    }

    .AlertTxt
    {
        margin: 0;
    }
 

