.bolly
{
    width: 100vw;
    max-width: 75rem;
    height: fit-content;
    margin : 3rem auto;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

 /* background-color: gray; */
}

.BollySongSec
{
    width: 100vw;
    max-width: 80rem;
    height: 17rem;
    /* background-color: aqua; */
    display: flex;
    /* flex-direction: row; */
    align-items: center;
    justify-content: center;
    margin-left: -2.3rem;
    margin-top: 1rem;
}

.aadatImg
{
    width: 100vw;
    max-width: 16rem;
    height: 10rem;
    border-radius: 1rem;
    cursor: pointer;
    margin-top: -1.5rem;  
}

.songname
{
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    margin-top: 0.5rem;
}

.artistname
{
    font-size: 1rem;
    cursor: pointer;
    
}

.bollyheading
{
    /* background-color: bisque; */

    background-color: #303440;
    font-weight: bold;
    color: white;
    width: 19rem;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;




}
.BlueLine
{
    height: 3px;
    background-color: #4ecdff;
    width: 19rem;
    margin: 0 auto;

}

.song1
{
    width: 100vw;
    max-width: 16rem;
    height: 14rem;
    background-color: rgb(246, 246, 246);
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    border-radius: 1rem;
    box-shadow: 2px 5px 4px 1px rgba(0,0,0,.2);
    margin: 1rem;
    transition: transform 0.5s;

    
}

.song1:hover
{
    transform: scale(1.1);
}

