@media only screen and (max-width: 768px) {
    .bolly {
        padding: 0;

    }

    .BollySongSec
    {
        display: flex;
        flex-wrap: wrap;
        height:54vh;
        width:22rem;
        margin: 0 auto;

    }

    .song1
    {
        width: 100vw;
        max-width: 10rem;
        height: 8rem;
        margin: 0 0.5rem;
    


    }
    .song2
    {
        width: 100vw;
        max-width: 10rem;
        height: 8rem;
        margin: 0;
        margin: 0 0.5rem;
    }

    .song3
    {
        width: 100vw;
        max-width: 10rem;
        height: 8rem;
        margin: 0;
        margin: 0 0.5rem;

    }

    .song4
    {
        width: 100vw;
        max-width: 10rem;
        height: 8rem;
        margin: 0;
        margin: 0 0.5rem;
        
    }


    .aadatImg
    {
        width: 100vw;
        max-width: 10rem;
        height: 5rem;
    }

    .songname
    {
        font-size: medium;
        text-align: center;
    }

    .bollyheading
    {
        background-color:  #303440;
        color: white;
        margin: 0;
        margin: 0 auto; 
        width:20rem;
        text-align: center;
    }

    .BlueLine
    {
        width: 20rem;
        margin: 0 auto;
        
    }   
}